import { Link } from 'react-router-dom';
import { ImCodepen } from 'react-icons/im';
import { FaBars, FaTimes } from 'react-icons/fa';
import { IconContext } from 'react-icons';
import { useState, useEffect } from 'react';
import { Button } from './Button';
import './Navbar.css';
import { GoMarkGithub } from 'react-icons/go';
import { AiFillLinkedin } from 'react-icons/ai';

const Navbar = () => {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    window.addEventListener('resize', showButton);
    return () => {
      window.removeEventListener('resize', showButton);
    };
  }, []);

  return (
    <IconContext.Provider value={{ color: '#fff' }}>
      <nav className="navbar">
        <div className="navbar-container container">
          <Link to="/" className="navbar-logo" onClick={() => setClick(false)}>
            <ImCodepen className="navbar-icon" />
            Shubh Bhalla
          </Link>
          <div className="menu-icon">
            {click ? (
              <FaTimes onClick={() => setClick(!click)} />
            ) : (
              <FaBars onClick={() => setClick(!click)} />
            )}
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className="nav-item">
              <a
                className="nav-links"
                href="https://www.linkedin.com/in/shubh-bhalla-b86693a7/"
                target="_blank"
                onClick={() => setClick(false)}
              >
                <AiFillLinkedin />
              </a>
              {/* <Link
                to="/"
                className="nav-links"
                onClick={() => setClick(false)}
              >
                Home
              </Link> */}
            </li>
            <li className="nav-item">
              {/* <Link
                to="/"
                className="nav-links"
                onClick={() => setClick(false)}
              > */}
              <a
                className="nav-links"
                href="https://shubh-food-recognition.herokuapp.com/"
                target="_blank"
                onClick={() => setClick(false)}
              >
                Food Recognition App
              </a>
              {/* </Link> */}
            </li>
            <li className="nav-item">
              <a
                className="nav-links"
                href="https://github.com/shubhbhalla"
                target="_blank"
                onClick={() => setClick(false)}
              >
                <GoMarkGithub />
              </a>
            </li>
            {/* <li className="nav-btn">
              {button ? (
                <Link to="/signup" className="btn-link">
                  <Button buttonStyle="btn--outline">Sign Up</Button>
                </Link>
              ) : (
                <Link
                  to="/signup"
                  className="btn-link"
                  onClick={() => setClick(false)}
                >
                  <Button buttonStyle="btn--outline" buttonSize="btn--mobile">
                    Sign Up
                  </Button>
                </Link>
              )}
            </li> */}
          </ul>
        </div>
      </nav>
    </IconContext.Provider>
  );
};

export default Navbar;
